import React, { useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { Link } from 'react-router-dom'
import { Row, Col, ListGroup, Image, Form, Button, Card, ListGroupItem, FormControl } from 'react-bootstrap'
import Message from '../components/Message'
import { addToCart, removeFromCart } from '../actions/cartActions'
import { useParams } from 'react-router-dom'
import { useNavigate } from 'react-router-dom';
import { useLocation } from 'react-router-dom'
import { useSearchParams } from 'react-router-dom'
import ShippingScreen from './ShippingScreen'
import { login } from '../actions/userActions'




const CartScreen = ({ }) => {
  const {id} = useParams()
  const navi =  useNavigate()
  
  const search = useSearchParams()
  const location = useLocation()

const productId = id

const qty = location.search ? Number(location.search.split( '=' )[1]) : 1

const userLogin = useSelector(state => state.userLogin)
    const { userInfo } = userLogin

const dispatch = useDispatch()


const cart = useSelector((state) => state.cart)
const { cartItems } = cart 
 



useEffect(() => {
  if (productId) {
    dispatch( addToCart(productId, qty))
  }
}, [dispatch, productId, qty])


const removeFromCartHandler = (id) => {
  dispatch(removeFromCart(id))
}

const checkoutHandler = () => {
  if(userInfo) {
  navi('/shipping')
  } else 
  {
    navi('/login')
  }
  
  // ERROR : not redirecting to shipping
}



return  <Row>
   <Col md={8}>
   <h1>Shopping Cart</h1>
   {cartItems.length === 0 ? (<Message>Your cart is empty <Link to='/'>Go Back</Link></Message> ): ( <ListGroup variant='flush'>
    {cartItems.map(item => (<ListGroupItem key={item.product}>
      <Row>
        <Col md={2}>
          <Image src={item.image} alt={item.name} fluid rounded />
        </Col>
        <Col md={3}>
          <Link to={`/product/${item.product}`}>{item.name}</Link>
        </Col>
        <Col md={2}>${item.price}</Col>
        <Col md={2}>
        <FormControl as='select' value={item.qty} onChange={(e) =>
           dispatch(addToCart(item.product, Number(e.target.value))) }>

             { [...Array(item.countInStock).keys()].map((x ) => (<option key={ x + 1 } value={ x + 1 }> { x + 1} </option>))}
            </FormControl>

        </Col>
        <Col md={2}>
          <Button type='button' variant='light' onClick={() => removeFromCartHandler(item.product)}><i className='fas fa-trash'></i>  </Button>
        </Col>
      </Row>
    </ListGroupItem>))}
   </ListGroup>  ) }
   </Col>
   
   <Col md={4}>
    <Card>
      <ListGroup variant='flush'>
        <ListGroupItem>
          <h3>Subtotal ({cartItems.reduce((acc, item) => acc + item.qty, 0 )}) items </h3>
          ${cartItems.reduce((acc, item) => acc + item.qty * item.price, 0 ).toFixed(2)}
        </ListGroupItem>
        <ListGroupItem>
          <Button type='button' className='btn-block' disabled={cartItems.length === 0} onClick={checkoutHandler}>Procees To Checkout</Button>
        </ListGroupItem>
      </ListGroup>
    </Card>

   </Col>

</Row>

}



export default CartScreen
